	module.exports = {"data":{"site":{"siteMetadata":{"title":"XXX Test Blog","author":"dice27"}},"markdownRemark":{"id":"/Users/yudai/work/gatsby_study/src/pages/2018-08-19/index.md absPath of file >>> MarkdownRemark","html":"<p>This is my first post on my new fake blog!<br>\nHow exciting!</p>\n<div class=\"gatsby-highlight\" data-language=\"ruby\">\n      <pre class=\"language-ruby\"><code class=\"language-ruby\"><span class=\"token comment\">#!/usr/bin/env ruby</span>\n\nputs <span class=\"token string\">'Hello World.'</span></code></pre>\n      </div>\n<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. In diam velit, lobortis at turpis sed, ultricies auctor nulla. Aenean vestibulum ac enim vel pretium.</p>\n<hr>\n<h3>Penguins</h3>\n<p>Oh, and here’s a great quote from this Wikipedia on\n<a href=\"https://en.wikipedia.org/wiki/Penguin\">penguins</a>.</p>\n<blockquote>\n<p>Penguins (order Sphenisciformes, family Spheniscidae) are a group of aquatic, flightless birds.<br>\nThey live almost exclusively in the Southern Hemisphere, with only one species, the Galapagos penguin, found north of the equator.<br>\nHighly adapted for life in the water, penguins have countershaded dark and white plumage, and their wings have evolved into flippers.<br>\nMost penguins feed on krill, fish, squid and other forms of sea life which they catch while swimming underwater.<br>\nThey spend roughly half of their lives on land and the other half in the sea.  </p>\n</blockquote>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/gatsby_study/static/penguins-3045674c5862cc5e13bb929f18c36260-90e0b.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Penguins\"\n        title=\"\"\n        src=\"/gatsby_study/static/penguins-3045674c5862cc5e13bb929f18c36260-f8fb9.jpg\"\n        srcset=\"/gatsby_study/static/penguins-3045674c5862cc5e13bb929f18c36260-e8976.jpg 148w,\n/gatsby_study/static/penguins-3045674c5862cc5e13bb929f18c36260-63df2.jpg 295w,\n/gatsby_study/static/penguins-3045674c5862cc5e13bb929f18c36260-f8fb9.jpg 590w,\n/gatsby_study/static/penguins-3045674c5862cc5e13bb929f18c36260-85e3d.jpg 885w,\n/gatsby_study/static/penguins-3045674c5862cc5e13bb929f18c36260-d1924.jpg 1180w,\n/gatsby_study/static/penguins-3045674c5862cc5e13bb929f18c36260-9452e.jpg 1770w,\n/gatsby_study/static/penguins-3045674c5862cc5e13bb929f18c36260-90e0b.jpg 1920w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"title":"Hello World - ハローワールド","date":"August 19, 2018"}}},"pathContext":{"slug":"/2018-08-19/","previous":null,"next":null}}

